.card {
  display: flex;
  width: 30%;
  text-align: center;
  margin: auto;
  align-items: center;
  border-radius: 15px;
  padding: 10px;
  box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.3);
  cursor: pointer;
  margin-bottom: 10px;
}
.card:hover {
  box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 1);
}

img {
  max-height: 100px;
  width: 6rem;
}

h3 {
  text-align: center;
  align-items: center;
  display: flex;
  margin: auto;
}
